/* 
* =======================
* =======================
* Application Themes
* =======================
* =======================
*/

/* Backgrounds */

.fourth_color {
  background: #198bca;
}

.primary_color {
  background: midnightblue;
}

.secondary_color {
  color: #f7cf56;
  background: #e8e9ef;
}

.tertiary_color {
  background: #ffffff;
}

/* Text and Font */
.primary_text_color {
  color: #fefefe;
}

.secondary_text_color {
  color: #f7cf56;
}

.tertiary_text_color {
  color: #44507f;
}

.primary_heading_color {
  color: midnightblue;
}

input[type="text"] {
  color: #44507f;
  background: #e8e9ef;
}

input[type="text"]:focus {
  color: darkgoldenrod;
  background: #e8e9ef;
}

textarea[type="text"] {
  color: #44507f;
  background: #e8e9ef;
}

textarea[type="text"]:focus {
  color: darkgoldenrod;
  background: #e8e9ef;
}

/* 
* =======================
* =======================
* ParticleView Component
* =======================
* =======================
*/

.particle_container {
  background: midnightblue;
  padding: 0;
  margin: 0;
  height: 100vh;
}

.particle_container_title {
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

/* 
* =======================
* =======================
* Navbar Component
* =======================
* =======================
*/

.active_nav {
  /* background: red; */
  border-bottom: 1px solid darkgoldenrod;
}

/* 
* =======================
* =======================
* Summary Component
* =======================
* =======================
*/

#summary_container {
  background: #ffffff;
  overflow: hidden;
  max-width: 100vw;
  /* max-height: 800px; */
}

.summary_container_secondary_text_color {
  color: #44507f;
}

#summary_profile_picture {
  max-height: 90vh;
}

#summary_text {
  min-height: 80vh;
}

.image_container {
  overflow: hidden;
}

/* 
* =======================
* =======================
* Portfolio Component
* =======================
* =======================
*/

#portfolio_container {
  min-height: 80vh;
  padding-top: 20vh;
  padding-bottom: 20vh;
}

.company_card {
  min-height: 200px;
}

.company_card_image {
  height: 300px;
  width: 300px;
}

.animation_top {
  top: 150px;
  position: relative;
  /* background: red; */
}

.animation_bottom {
  bottom: 100px;
  position: relative;
}

/* 
Modal stuff below found for the Recipe Sections
*/

.modalx {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

#modal-main {
  position: fixed;
  color: #44507f;
  background: #ffffff;
  /* min-width: 640px; */
  max-width: 28vw;
  height: 89vh;
  top: 9%;
  left: 30%;
  overflow: scroll;
  padding: 1%;
  text-align: left;
}

.modal_image {
  width: 25vw;
  height: 25vw;
  padding: 0px 0px 10px 0px;
}

.modal_hr {
  padding: 0px;
  margin: 0px 0px 8px 0px;
}

hr {
  background: #fefefe;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

/* BLOG */

.blog_image {
  height: 30vh;
  width: 50vh;
}

.blog_box {
  height: 60vh;
  width: 50vh;
  background: #fefefe;
}

.blog_post_active h4 {
  color: #198bca;
}

.blog_post_active h6 {
  color: #198bca;
}

.blog_post_active p {
  color: #198bca;
}

/* CODE SNIPPET */

/* CodeSnippets.js */
#code_snippet_container {
  padding-top: 10vh;
  padding-bottom: 20vh;
  text-align: center;
}

/* CodeSnippets.js */
#code_snippet_box {
  min-height: 50vh;
  background: #ffffff;
  text-align: center;
  padding: 2%;
}

/* CodeSnippets.js */
#code_snippet_previous_button {
  color: white;
  background: #198bca;
  height: 40px;
  width: 60px;
  top: 30vh;
  right: 4vh;
  position: relative;
}

/* CodeSnippets.js */
#code_snippet_next_button {
  color: white;
  background: #198bca;
  height: 40px;
  width: 60px;
  top: 30vh;
  left: 4vh;
  position: relative;
}

/* CodeSnippets.js */
.code_snippet_image {
  max-height: 700px;
  max-width: 800px;
}

/* FOOTER COMPONENT */
.contact_container {
  min-height: 350px;
}

.footer {
  padding-top: 15vh;
  padding-bottom: 1vh;
  background: midnightblue;
  text-align: center;
  color: white;
}

.subfooter {
  background: #021a34;
  color: darkgoldenrod;
  text-align: center;
  padding-top: 3vh;
  padding-bottom: 1vh;
}

.subfooter_references {
  color: black;
}
